import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import Container from '@material-ui/core/Container';

const useStyles = makeStyles({
  root: {
    // maxWidth: 345,
  },
});

export default function CompanyDescription() {
  const classes = useStyles();

  return (
    <Container maxWidth="sm">
      <Card className={classes.root}>
        <CardActionArea>                   
          <CardContent>
            <Typography gutterBottom variant="h5" component="h2">
               УМНАЯ АВТОМАТИЗАЦИЯ ВАШЕГО БИЗНЕСА
            </Typography>
            <Typography variant="body2" color="textSecondary" component="p">
              Черезвычайно красивое описание проекта и того, как здорово можно упростить себе жизнь, обратившись к нам.
              Короче тут должен быть некий "продающий текст", мол несите нам деньги и у вас будет больше денег.
              Или вообще абстрактная писанина.
            </Typography>
          </CardContent>
        </CardActionArea>
        <CardActions>
          <Button size="big" color="primary">
            Оставить заявку
          </Button>          
        </CardActions>
      </Card>
    </Container>
  );
}